/* harmony default export */ var login = ({
  data: function data() {
    return {
      phoneNum: '', // 手机号
      picCodeNum: '', // 图形验证码
      picture_code: '', // 图形验证码图片
      sms_code: '', // 手机验证码
      verify_ticket: '',
      disabled: false, // 是否禁用验证按钮
      time: 0, // 默认倒计时秒数
      second: 60, // 倒计时
      swiperPopup: false // 易盾滑块弹窗
    };
  },
  mounted: function mounted() {
    var that = this;
    that.$nextTick(function () {
      // this.init()
    });
  },

  methods: {
    // 易盾初始化
    init: function init() {
      var that = this;
      /* eslint-disable */
      initNECaptcha({
        // config对象，参数配置
        captchaId: 'd217d61693664001a3b6341eb5360e22',
        element: '#captcha',
        mode: 'float',
        width: '320px',
        onReady: function onReady(instance) {
          // 验证码一切准备就绪，此时可正常使用验证码的相关功能
        },
        onVerify: function onVerify(err, data) {
          /**
          * 第一个参数是err（Error的实例），验证失败才有err对象
          * 第二个参数是data对象，验证成功后的相关信息，data数据结构为key-value，如下：
          * {
          *   validate: 'xxxxx' // 二次验证信息
          * }
          */
          var dataSMS = {
            phone: that.phoneNum,
            token: that.$store.state.token,
            NECaptchaValidate: data.validate
          };
          console.log('5555555555', dataSMS);
          that.axios({
            method: 'POST',
            url: 'interface/Login/sendCode',
            data: dataSMS
          }).then(function (res) {
            console.log('发送验证码', res);
            if (res.code === 1) {
              that.verify_ticket = res.data.verify_ticket;
              that.time = that.second;
              that.disabled = true;
              console.log(that.disabled);
              that.swiperPopup = false;
              that.timer();
            } else {
              that.showAlert(res.msg);
            }
          });
        }
      }, function onload(instance) {
        // 初始化成功后得到验证实例instance，可以调用实例的方法
        console.log('instance', instance);
      }, function onerror(err) {
        // 初始化失败后触发该函数，err对象描述当前错误信息
      });
    },

    // 关闭弹窗
    closePopup: function closePopup() {
      this.swiperPopup = false;
    },

    // 验证码倒计时
    getCodeClick: function getCodeClick() {
      var that = this;
      if (that.phoneNum === '') {
        that.showAlert('请输入手机号');
        return false;
      }
      that.swiperPopup = true;
      that.init();
    },
    timer: function timer() {
      if (this.time > 0) {
        this.time--;
        setTimeout(this.timer, 1000);
      } else {
        this.disabled = false;
      }
    },

    // 立即登入
    loginClick: function loginClick() {
      var that = this;
      // if (that.picture_code === '') {
      //   this.showAlert('请输入图形验证码')
      //   return false
      // }
      if (that.sms_code === '') {
        this.showAlert('请输入手机验证码');
        return false;
      }
      if (that.sms_code === '') {
        this.showAlert('请输入手机号');
        return false;
      }
      var data = {
        verify_ticket: that.verify_ticket,
        sms_code: that.sms_code,
        phone: that.phoneNum,
        token: that.$store.state.token
      };
      this.axios({
        method: 'POST',
        url: 'interface/Login/bindPhone',
        data: data
      }).then(function (res) {
        console.log('登入', res);
        if (res.code === 1) {
          localStorage.setItem('bind', res.bind);
          that.$router.push('/');
          console.log(111222, that.$router);
        } else {
          that.showAlert(res.msg);
        }
      });
    }
  },
  computed: {
    countDown: function countDown() {
      return this.time > 0 ? this.time + 's 后重获取' : '获取验证码';
    }
  }
});
